'use client';

import { useAuthContext } from '@/lib/auth-context';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Skeleton } from '@/components/ui/skeleton';
import { PlaceHolderImages } from '@/lib/placeholder-images';
import { fetchUserStats, fetchUserPosts } from '@/lib/api/posts';
import { Edit, Mail, Calendar, MapPin } from 'lucide-react';
import { useState, useEffect } from 'react';
import type { Post } from '@/lib/types';

interface UserStats {
  stats: {
    posts: number;
    following: number;
    followers: number;
  };
  createdAt: string;
}

export default function ProfilePage() {
  const { user, token } = useAuthContext();
  const [userStats, setUserStats] = useState<UserStats | null>(null);
  const [userPosts, setUserPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchProfileData = async () => {
      if (!user || !token) {
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);

        // Fetch user stats
        const statsData = await apiRequest<UserStats>('/api/users/me', {
          method: 'GET',
          token,
        });
        setUserStats(statsData);

        // Fetch user's posts
        const postsData = await apiRequest<PostsResponse>('/api/posts/me', {
          method: 'GET',
          token,
        });
        setUserPosts(postsData.posts || []);
      } catch (err) {
        console.error('Failed to fetch profile data:', err);
        setError('Failed to load profile data');
      } finally {
        setLoading(false);
      }
    };

    fetchProfileData();
  }, [user, token]);

  const formatJoinDate = (dateString: string) => {
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long'
      });
    } catch {
      return 'Unknown';
    }
  };

  const formatPostDate = (dateString: string) => {
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    } catch {
      return 'Unknown date';
    }
  };

  if (!user) {
    return (
      <div className="container mx-auto px-4 py-8">
        <Card className="max-w-2xl mx-auto">
          <CardContent className="text-center p-8">
            <p className="text-muted-foreground">Please log in to view your profile.</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <Card className="max-w-2xl mx-auto">
          <CardContent className="text-center p-8">
            <p className="text-destructive">{error}</p>
            <Button 
              onClick={() => window.location.reload()} 
              variant="outline" 
              className="mt-4"
            >
              Retry
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-4xl mx-auto space-y-8">
        {/* Profile Header */}
        <Card>
          <CardContent className="p-8">
            <div className="flex flex-col md:flex-row items-center md:items-start gap-6">
              <Avatar className="h-32 w-32">
                <AvatarImage src={PlaceHolderImages[0].imageUrl} alt={user.displayName ?? user.email} />
                <AvatarFallback className="text-2xl">
                  {user.displayName?.charAt(0).toUpperCase() ?? user.email.charAt(0).toUpperCase()}
                </AvatarFallback>
              </Avatar>
              
              <div className="flex-1 text-center md:text-left">
                <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-4">
                  <div>
                    <h1 className="text-3xl font-bold">{user.displayName ?? 'Anonymous User'}</h1>
                    <p className="text-muted-foreground">@{user.email.split('@')[0]}</p>
                  </div>
                  <Button variant="outline" className="mt-4 md:mt-0">
                    <Edit className="h-4 w-4 mr-2" />
                    Edit Profile
                  </Button>
                </div>
                
                <div className="space-y-2 text-sm text-muted-foreground">
                  <div className="flex items-center justify-center md:justify-start">
                    <Mail className="h-4 w-4 mr-2" />
                    {user.email}
                  </div>
                  <div className="flex items-center justify-center md:justify-start">
                    <Calendar className="h-4 w-4 mr-2" />
                    {loading ? (
                      <Skeleton className="h-4 w-32" />
                    ) : (
                      `Joined ${userStats ? formatJoinDate(userStats.createdAt) : 'Unknown'}`
                    )}
                  </div>
                  <div className="flex items-center justify-center md:justify-start">
                    <MapPin className="h-4 w-4 mr-2" />
                    Location not set
                  </div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Profile Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <Card>
            <CardHeader className="text-center">
              <CardTitle className="text-2xl">
                {loading ? (
                  <Skeleton className="h-8 w-8 mx-auto" />
                ) : (
                  userStats?.stats.posts || 0
                )}
              </CardTitle>
              <CardDescription>Posts</CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="text-center">
              <CardTitle className="text-2xl">
                {loading ? (
                  <Skeleton className="h-8 w-8 mx-auto" />
                ) : (
                  userStats?.stats.following || 0
                )}
              </CardTitle>
              <CardDescription>Following</CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="text-center">
              <CardTitle className="text-2xl">
                {loading ? (
                  <Skeleton className="h-8 w-8 mx-auto" />
                ) : (
                  userStats?.stats.followers || 0
                )}
              </CardTitle>
              <CardDescription>Followers</CardDescription>
            </CardHeader>
          </Card>
        </div>

        {/* Recent Activity */}
        <Card>
          <CardHeader>
            <CardTitle>Recent Activity</CardTitle>
            <CardDescription>Your latest posts and interactions</CardDescription>
          </CardHeader>
          <CardContent>
            {loading ? (
              <div className="space-y-4">
                {Array.from({ length: 3 }).map((_, i) => (
                  <div key={i} className="space-y-2">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-3/4" />
                    <Skeleton className="h-3 w-1/4" />
                  </div>
                ))}
              </div>
            ) : userPosts.length > 0 ? (
              <div className="space-y-4">
                {userPosts.slice(0, 5).map((post) => (
                  <div key={post.id} className="border-l-2 border-primary/20 pl-4 py-2">
                    <p className="text-sm mb-2">{post.text}</p>
                    <div className="flex items-center gap-2 text-xs text-muted-foreground">
                      <span>{formatPostDate(post.timestamp)}</span>
                    </div>
                  </div>
                ))}
                {userPosts.length > 5 && (
                  <div className="text-center pt-4">
                    <Button variant="outline" size="sm">
                      View All Posts ({userPosts.length})
                    </Button>
                  </div>
                )}
              </div>
            ) : (
              <div className="text-center py-8 text-muted-foreground">
                <p>No recent activity to show.</p>
                <p className="text-sm mt-2">Start posting to see your activity here!</p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}